@import url("https://fonts.googleapis.com/css2?family=Heebo:wght@100;200;300;400;500;600;700;800;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=DM+Sans&family=Lobster:wght@500&family=Nunito:ital,wght@0,300;0,400;0,800;1,300;1,400;1,500&family=Lobster&family=Roboto:ital,wght@0,100;0,400;0,500;1,300&family=Satisfy&display=swap");


:root {
  --one-blue: #00a6fb;
  --two-blue: #0582ca;
  --three-blue: #006494;
  --four-blue: #003554;
  --five-blue: #051923;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Heebo', sans-serif;
}

body {
  color: black;
}

.content {
  padding: 0px 200px;
}

@media only screen and (max-width: 1600px) {
  .content {
    padding: 0px 50px;
  }
}

@media only screen and (max-width: 600px) {
  .content {
    padding: 10px;
  }
}